$cell-size: 32px;

.cell {
  background-color: darkgrey;
  border-left: var(--cell-border);
  border-top: var(--cell-border);
  font: inherit;
  font-size: 18px;
  font-weight: bold;
  height: $cell-size;
  line-height: $cell-size;
  text-align: center;
  width: $cell-size;
}

.visible {
  background-color: lightgrey;
}

.busted {
  background-color: red;
}

.wrong {
  position: relative;

  &::before,
  &::after {
    background: red;
    content: '';
    height: 100%;
    left: 45%;
    position: absolute;
    top: 0;
    width: 2px;
  }

  &::before {
    transform: rotate(-45deg);
  }

  &::after {
    transform: rotate(45deg);
  }
}

.value_1 {
  color: blue;
}

.value_2 {
  color: green;
}

.value_3 {
  color: red;
}

.value_4 {
  color: purple;
}

.value_5 {
  color: maroon;
}

.value_6 {
  color: turquoise;
}

.value_7 {
  color: black;
}

.value_8 {
  color: darkgrey;
}

:export {
  cellSize: $cell-size;
}
